body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: left ;
}

.MotherOfAllSearchViews {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: left;
          align-items: left;
  -webkit-justify-content: left;
          justify-content: left;
  font-size: calc(3px + 2vmin);
  color: white;
  padding: 1vmin;
}

.Header {
  background-color: #fdba28;
  padding: 1vmin;

  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.Footer {
  position: -webkit-sticky;
  position: sticky;
}

.SearchBar {
  padding-bottom: 0vmin;
}

p {
  border: 1px solid powderblue;
  padding: 30px;
}

input {
  font-size: calc(10px + 2vmin);
  -webkit-animation-name: blinkBox;
          animation-name: blinkBox;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-iteration-count: 2;
          animation-iteration-count: 2;
}

@-webkit-keyframes blinkBox {
  0%   {background-color:rgb(255, 255, 255); left:0px; top:0px;}
  100% {background-color:rgb(19, 3, 3); left:0px; top:0px;}
}

@keyframes blinkBox {
  0%   {background-color:rgb(255, 255, 255); left:0px; top:0px;}
  100% {background-color:rgb(19, 3, 3); left:0px; top:0px;}
}
